import React, { Component } from 'react'
import { Base64 } from 'js-base64';
import { Table , Input , Button , Breadcrumb , Form , Select,message, DatePicker,Spin,Modal} from 'antd';
const FormItem = Form.Item;
const Search = Input.Search;
const Option = Select.Option;
const RangePicker = DatePicker.RangePicker;
const confirm = Modal.confirm;
const { TextArea } = Input;

class TopForm extends Component {
  constructor(props) {
    super(props)
  }
  handleSearch =()=>{
    this.props.form.validateFields((err, values) => {
      let arr = Object.keys(values);
      let data = {}
      for(let i = 0;i<arr.length;i++){
        if(arr[i] == 'chargeTime'){
          values[arr[i]] && (data.chargeTimeStart = new Date(values[arr[i]][0]._d).getTime());
          values[arr[i]] && (data.chargeTimeEnd = new Date(values[arr[i]][1]._d).getTime());
        }else if(arr[i] == 'deadlineDate'){
          values[arr[i]] && (data.deadlineDateStart = new Date(values[arr[i]][0]._d).getTime());
          values[arr[i]] && (data.deadlineDateEnd = new Date(values[arr[i]][1]._d).getTime());
        }else{
          (values[arr[i]]) && (data[arr[i]] = values[arr[i]])
        }
      }
      if(data){
        //
        this.props.getSearch(data);
      }
    });
  }
  clear = ()=>{
    this.props.form.resetFields();
    this.props.init({})
  }
  render(){
    const { getFieldDecorator, resetFields } = this.props.form;
    const rangeConfig = {
      rules: [{ type: 'array',  message: '!' }],
    };
    return (
      <Form className = 'topForm clean'>
            <div className = 'fl'>
              <FormItem label = '' className = 'formItem'>
                {getFieldDecorator('chargeTime', rangeConfig)(
                  <RangePicker format="YYYY-MM-DD" placeholder={['', '']}/>
                )}
              </FormItem>
              <FormItem label = '' className = 'formItem'>
              {getFieldDecorator('deadlineDate', rangeConfig)(
                <RangePicker format="YYYY-MM-DD" placeholder={['', '']}/>
              )}
              </FormItem>
              
              </div>
              <div className = 'fl'>
                <FormItem label = '' className = 'formItem'>
                {getFieldDecorator('teamName')(
                  <Input />
                )}
                </FormItem>
                <FormItem label = '' className = 'formItem'>
                {getFieldDecorator('inputManName')(
                  <Input />
                )}
                </FormItem>
              
              </div>
              <div className = 'fl'>
                <FormItem label = '' className = 'formItem'>
                  {getFieldDecorator('vehicleId')(
                    <Input />
                  )}
                </FormItem>
          
              </div>
            <div className = 'fl'>
              <a className = 'empty' onClick = {this.clear} ></a>
            </div>
            <div className = 'fr'>
              <Button type="primary" onClick={this.handleSearch}></Button>
              <Button type="primary" onClick={this.props.exportForm}></Button>
            </div>
          </Form>
    )
  }
  
}
const SearchForm = Form.create({
  mapPropsToFields(props) {
    return {
      init: Form.createFormField({
        value: props.init,
      }),
      getSearch: Form.createFormField({
        value: props.getSearch,
      }),
      exportForm: Form.createFormField({
        value: props.exportForm,
      }),
    }
  },
})(TopForm)

class TMsgDetail extends Component{
  constructor(props) {
    super(props)
    this.state = {
      data:[]
    }
  }
  handleSubmit = ()=>{
    this.props.form.validateFields((err, values) => {
      if(!err){
        confirm({
          title: '',
          content: '',
          okText:'',
          cancelText:'',
          onOk:()=> {
            values.invoiceNum = values.prefix + values.invoiceNum;
            delete values.prefix;
            values.deadlineDate = new Date(this.props.detail.deadlineDate).getTime();
            values.chargeTime = window.$Funs.formatDate(values.chargeTime);
            values.id = this.props.detail.chargeid;
            values.newCarId = this.props.detail.newCarId;
            values.vehicleId = this.props.detail.vehicleId;
            values.teamName = this.props.detail.teamName;
            values.inputMan = window.$Funs.cook.get('id');
            values.inputManName = window.$Funs.cook.get('name');
            window.$Funs.$AJAX('charge','post',values,(res)=>{
              message.success('');
              this.props.cancel()
            })      
          },
          onCancel() {
          },
        });
        
      }
    });
  }
  render(){
    const { getFieldDecorator} = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
      },
    };
    const prefixSelector = getFieldDecorator('prefix', {
      initialValue: 'F',
    })(
      <Select style={{ width: 50 }}>
        <Option value="F">F</Option>
        <Option value="S">S</Option>
      </Select>
    );
    let msgform = (
      <div className = 'detail'>
        <Form layout="inline"  className='clean'>
          <div className = 'clean'>
            <FormItem label='' className = 'formItem clean'>
              <Input  value={this.props.detail.vehicleId } disabled className = 'disabled'/>
            </FormItem>
            <FormItem label='' className = 'formItem clean'>
              <Input  value={this.props.detail.teamName } disabled className = 'disabled'/>
            </FormItem>
            <FormItem className = 'formItem clean'{...formItemLayout} label="">
              {getFieldDecorator('moneyAmont', {
                rules: [ {
                  required: true, message: '',
                }],
              })(
                <Input className = 'pay' />
              )}
              <span  className = 'unit'></span>
            </FormItem>
          </div>
          <div className = ' clean'>
            
            <FormItem className = 'formItem clean'{...formItemLayout} label="">
              {getFieldDecorator('payType', {
                rules: [ {
                  required: true, message: '',
                }],
                initialValue:''
              })(
                <Select  style={{ width: 200 }}>
                  <Option value=""></Option>
                  <Option value=""></Option>
                  <Option value=""></Option>
                  <Option value=""></Option>
                  <Option value=""></Option>
                  <Option value=""></Option>
                </Select>     
              )}
            </FormItem>
            <FormItem className = 'formItem clean'{...formItemLayout} label="">
              {getFieldDecorator('deadlineDate', {
                rules: [ {
                  required: true, message: '',
                }],
              })(
                <DatePicker />
              )}
            </FormItem>
          </div>
          <div className = 'clean'>
            <FormItem className = 'formItem code clean' {...formItemLayout} label="">
              {getFieldDecorator('invoiceNum', {
                rules: [ {
                  required: true, message: '',
                }],
              })(
                <Input  addonBefore={prefixSelector} style={{ width: '100%' }} />
              )}
            </FormItem>
          </div>
          <FormItem label = '' className = 'formItem fl clean'>
            {getFieldDecorator('remark', {
              rules: [ {
                required: true, message: '',
              }],
            })(
              <TextArea rows={3} />
            )}
          </FormItem>
        </Form>
        <div className = 'diaBtns fr'>
          <Button type="primary" onClick = {this.handleSubmit}></Button>
          <Button onClick = { this.props.cancel}></Button>
        </div>
      </div>
    )
    return(
      <div className = 'dialog'>
        <div className = 'mask'></div>
        <div className = 'main'>
          <p className = 'title'></p>
           {  msgform }
        </div>
      </div>
    )
  }
}
const MsgDetail = Form.create({
  mapPropsToFields(props) {
    return {
      detail: Form.createFormField({
        value: props.detail,
      }),
      cancel: Form.createFormField({
        value: props.cancel,
      }),
    }
  },
})(TMsgDetail)





export default class Modify extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading:true,
      showDiglog:false,
      currPage:1,
      pageSize:13,
      keyWord:{},//
      data:[],//table
      total:'',//
      detail:{},//
      selectedRows:[]
    }
  }
  componentWillMount(){
    this.init({})
  }
  init=(data)=>{
    !data.currPage && (data.currPage = this.state.currPage);
    data.pageSize = this.state.pageSize;
    window.$Funs.$AJAX('getChargeComplex','get',data,(res)=>{
      let data = res.data.map((v,i)=>{
        v.key = i;
        v.leaveFactoryInstall = v.leaveFactoryInstall == 0 ? '' : '';
        v.leaveFactoryDate = v.leaveFactoryDate.split(' ')[0];
        v.stop == 1 ? (v.stop = '') : (v.stop = '');
        v.stopTime ? (v.stopTime = window.$Funs.formatDate(v.stopTime)) : (v.stopTime = '');
        v.chargeTime ? (v.chargeTime = window.$Funs.formatDate(v.chargeTime)) : v.chargeTime = '';
        v.deadlineDate ? (v.deadlineDate = window.$Funs.formatDate(v.deadlineDate)) : v.deadlineDate = '';
        return v
      })
      this.setState({
        data : data,
        total: res.count,
        loading:false
      })
    })
  }
  getSearch=(data)=>{
    if(data){
      this.setState({
        keyWord:data,
        currPage:1,
        loading:true
      },()=>{
        this.init(data)
      })
    }
  }
  pageChange = (page)=>{
    this.setState({
      currPage:page,
      loading:true
    },()=>{
      let data = this.state.keyWord;
      this.init(data)
    })
  }
  edit = (item)=>{
    this.setState({
      showDiglog: true,
      detail:item
    })
  }
  cancel =()=>{
    this.setState({
      showDiglog:false
    })
  }
  exportForm = ()=>{
      if(this.state.selectedRows.length == 0){
        message.error('');
        return 
      }
      let exslDTO = {}
      exslDTO.ids = this.state.selectedRows.map(v=>{
        return v.chargeid 
      })
      exslDTO.maps = {
        "teamName":'',
        "vehicleId":'',
        "moneyAmont":'',
        "payType":'',
        "chargeTime":'',
        "deadlineDate":'',
        "invoiceNum":'',
        "inputManName":'',
      }
      exslDTO.type = 2;
      let code = Base64.encode(JSON.stringify(exslDTO))
      window.open(window.$Funs.Basse_Port+'saveExsl?exslDTO='+ code)
  }
  render() {
    const columns = [
      { title: '', dataIndex: 'teamName',key:'teamName',  width: 150 ,align: 'center' },
      { title: '', dataIndex: 'vehicleId',key:'vehicleId',  width: 100 ,align: 'center' },
      { title: '', dataIndex: 'moneyAmont',key:'moneyAmont',  width: 100 ,align: 'center' },
      { title: '', dataIndex: 'payType',key:'payType',  width: 100 ,align: 'center' },
      { title: '', dataIndex: 'chargeTime',key:'chargeTime' , width: 100 ,align: 'center' },
      { title: '', dataIndex: 'deadlineDate',key:'deadlineDate',  width: 100 ,align: 'center' },
      { title: '', dataIndex: 'invoiceNum',key:'invoiceNum',  width: 150 ,align: 'center' },
      { title: '', dataIndex: 'inputManName',key:'inputManName', width: 100 ,align: 'center' },
      { title: '', dataIndex: '', key: 'action', width: 100 ,align: 'center' , render: (item) => <Button type="primary" onClick = {()=>{this.edit(item)}}></Button> },
    ];
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        this.setState({
          selectedRows:selectedRows
        })
      },
    };
    
    return (
      <div className = 'modify'>
        <Spin spinning = {this.state.loading} size='large'>
          <SearchForm init={this.init} getSearch = {this.getSearch} exportForm = {this.exportForm}/>
          <Table rowSelection={rowSelection} expandedRowRender={record => <p style={{ margin: 0 }}>{record.remark}</p>} columns={columns} dataSource={this.state.data}  pagination = {{ defaultPageSize:13,total:this.state.total,onChange:this.pageChange,current:this.state.currPage }}/>
          {this.state.showDiglog && <MsgDetail detail = {this.state.detail} cancel = {this.cancel}/>}
        </Spin>
      </div>
    )
  }
}



